'use client';

import React, { useState } from 'react'; // <--- UPDATED THIS
import { supabase } from '@/lib/supabase';
import { useRouter } from 'next/navigation';
import { Shield } from 'lucide-react';

export default function LoginPage() {
  const [email, setEmail] = useState('');
  const [loading, setLoading] = useState(false);
  const [message, setMessage] = useState('');
  
  const handleLogin = async (e: React.FormEvent) => { // <--- Uses React.FormEvent
    e.preventDefault();
    setLoading(true);
    
    const { error } = await supabase.auth.signInWithOtp({
      email,
      options: {
        emailRedirectTo: `${window.location.origin}/dashboard`,
      },
    });

    if (error) {
      setMessage('Error: ' + error.message);
    } else {
      setMessage('Check your email for the login link!');
    }
    setLoading(false);
  };

  return (
    <div className="min-h-screen flex items-center justify-center bg-black px-4">
      <div className="w-full max-w-md space-y-8 bg-gray-900/50 p-8 rounded-2xl border border-gray-800 backdrop-blur-sm">
        <div className="text-center">
          <Shield className="mx-auto h-12 w-12 text-cyan-500" />
          <h2 className="mt-6 text-3xl font-extrabold text-white tracking-tight">
            Jackpot Intel
          </h2>
          <p className="mt-2 text-sm text-gray-400">
            Sign in to access the cycle.
          </p>
        </div>
        
        <form className="mt-8 space-y-6" onSubmit={handleLogin}>
          <div className="rounded-md shadow-sm -space-y-px">
            <div>
              <label htmlFor="email-address" className="sr-only">Email address</label>
              <input
                id="email-address"
                name="email"
                type="email"
                autoComplete="email"
                required
                className="appearance-none rounded-lg relative block w-full px-3 py-3 border border-gray-700 placeholder-gray-500 text-white bg-gray-800 focus:outline-none focus:ring-cyan-500 focus:border-cyan-500 sm:text-sm"
                placeholder="Email address"
                value={email}
                onChange={(e) => setEmail(e.target.value)}
              />
            </div>
          </div>

          <div>
            <button
              type="submit"
              disabled={loading}
              className="group relative w-full flex justify-center py-3 px-4 border border-transparent text-sm font-medium rounded-md text-black bg-cyan-500 hover:bg-cyan-400 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-cyan-500 disabled:opacity-50 transition-all shadow-[0_0_15px_rgba(34,211,238,0.3)]"
            >
              {loading ? 'Sending Magic Link...' : 'Enter the System'}
            </button>
          </div>
          
          {message && (
            <div className="text-center text-sm text-cyan-400 font-mono bg-cyan-900/20 p-2 rounded">
              {message}
            </div>
          )}
        </form>
      </div>
    </div>
  );
}